import { request } from '@/network/request';

// eslint-disable-next-line import/prefer-default-export
export function getAboutAll() {
  return request({
    url: 'goods',
  });
}
export function getAboutGoods(cid = 0, sort = 'sales', pages = 1) {
  return request({
    // eslint-disable-next-line camelcase
    url: `goods?category_id=${cid}&${sort}=1&page=${pages}`,
  });
}
export function getDetailAll(id) {
  return request({
    url: `goods/${id}`,
  });
}
