import { request } from '@/network/request';

// eslint-disable-next-line import/prefer-default-export
export function PostRegister(data) {
  return request({
    url: 'auth/register',
    method: 'post',
    data,
  });
}
export function PostUserLogin(data) {
  return request({
    url: 'auth/login',
    method: 'post',
    data,
  });
}

export function PostUserLogout() {
  return request({
    url: 'auth/logout',
    method: 'post',
  });
}
export function PostSetUser(username) {
  return request({
    url: 'user',
    method: 'put',
    data: {
      name: username,
    },
  });
}
export function GetUser() {
  return request({
    url: 'user',
  });
}
export function setUserImg() {
  return request({
    url: 'user/avatar',
  });
}
export function GetOssToken() {
  return request({
    url: 'auth/oss/token',
  });
}
export function SentEmailSms(email) {
  return request({
    url: 'auth/email/code',
    method: 'post',
    data: {
      email,
    },
  });
}
export function SentEmail(email, code) {
  return request({
    url: 'auth/email/update',
    method: 'post',
    data: {
      email,
      code,
    },
  });
}
export function SentPhoneNum(phone) {
  return request({
    url: 'auth/phone/code',
    method: 'post',
    data: {
      phone,
    },
  });
}
export function SentPhone(phone, code) {
  return request({
    url: 'auth/phone/update',
    method: 'PATCH',
    data: {
      phone,
      code,
    },
  });
}
// eslint-disable-next-line camelcase
export function SentPassword(old_password, password, password_confirmation) {
  return request({
    url: 'auth/password/update',
    method: 'POST',
    data: {
      old_password,
      password,
      password_confirmation,
    },
  });
}
// 找回密码验证码
export function findPassword(email) {
  return request({
    url: 'auth/reset/password/email/code',
    method: 'POST',
    data: {
      email,
    },
  });
}
// 找回密码
export function findPasswords(code, email, password, passwordConfirmation) {
  return request({
    url: 'auth/reset/password/email',
    method: 'POST',
    data: {
      code,
      email,
      password,
      passwordConfirmation,
    },
  });
}
