<template>
  <van-nav-bar title="用户登录" />
  <img src="../../assets/img/middle.png" alt="login">
<!--  登录界面-->
  <van-form @submit="onSubmit">
    <van-cell-group inset>
      <van-field
        v-model="email"
        name="email"
        label="用户名"
        placeholder="请输入邮箱"
        :rules="[{ required: true, message: '请输入邮箱' }]"
      />
      <van-field
        v-model="password"
        type="password"
        name="password"
        label="密码"
        placeholder="密码"
        :rules="[{ required: true, message: '请填写密码' }]"
      />
    </van-cell-group>
    <div style="margin: 16px;">
     <van-button round block color="linear-gradient(to right,#FFA101,#ff6034)" native-type="submit">
        登录
      </van-button>
    </div>
    <div style="margin: 16px;">
     <van-button round block color="linear-gradient(to right,#FFA101,#ff6034)" @click="registerS">
        还没有账号，去注册
      </van-button>
    </div>
  </van-form>
  <footers></footers>
</template>

<script>
import { ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import { PostUserLogin } from '@/network/user';
import { useRouter } from 'vue-router';
import { Toast } from 'vant';
import footers from '@/components/common/footers.vue';

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Login',
  components: {
    footers,
  },
  setup() {
    const email = ref('');
    const password = ref('');
    const router = useRouter();
    const onSubmit = (values) => {
      console.log('submit', values);
      PostUserLogin(values).then((res) => {
        console.log(res);
        window.localStorage.setItem('token', res.data.access_token); // 设置token保存在本地浏览器中
        Toast.success('登录成功');
        router.push({ path: '/indexMe' });
      });
    };
    function registerS() {
      router.push({ path: '/register' });
    }
    return {
      email,
      password,
      onSubmit,
      registerS,
    };
  },
};
</script>

<style scoped>
img{
  margin: 1.5rem 0;
  width: 300%;
}
</style>
