<template>
  <van-nav-bar title="绑定手机号" left-arrow @click-left="onClickLeft"  />
  <van-form @submit="onSubmit">
    <van-cell-group inset>
      <van-field
        v-model="email"
        name="email"
        label="更换邮箱"
        placeholder="更换邮箱"
        :rules="[{ required: true, message: '更换邮箱' }]"
      />
      <van-field
        v-model="emailCode"
        center
        clearable
        label="短信验证码"
        placeholder="请输入短信验证码"
      >
        <template #button>
          <van-button size="small" @click="phondeCodes" type="primary">发送验证码</van-button>
        </template>
      </van-field>
    </van-cell-group>
    <div style="margin: 16px;">
      <van-button round block type="primary" native-type="submit">
        更改
      </van-button>
    </div>
  </van-form>
</template>

<script>
import { ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import { SentEmail, SentEmailSms } from '@/network/user';

export default {
  name: 'SetEmail',
  setup() {
    const email = ref('');
    const emailCode = ref('');
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const phondeCodes = () => {
      SentEmailSms(email.value).then((res) => {
        console.log(email);
        console.log(res);
      });
    };
    const onSubmit = (content) => {
      SentEmail(email.value, emailCode.value).then((res) => {
        console.log(res);
        if (res.status === 204) {
          // eslint-disable-next-line no-restricted-globals
          history.back();
        }
      });
      console.log(content);
    };
    return {
      onClickLeft,
      phondeCodes,
      emailCode,
      email,
      onSubmit,
    };
  },
};
</script>

