<template>
  <van-nav-bar title="地址管理" left-arrow @click-left="onClickLeft" />
  <van-address-list
    @select="switchAddress"
    v-model="chosenAddressId"
    :list="list"
    :switchable="switchable"
    default-tag-text="默认"
    @add="onAdd"
    @edit="onEdit"
  />
</template>

<script>
import { ref, onMounted } from 'vue';
import { useRouter, useRoute } from 'vue-router';
// eslint-disable-next-line import/no-cycle
import { getAddress, UpdateAddressDefault } from '@/network/address';
import { Toast } from 'vant';

export default {
  name: 'AddressList',
  setup() {
    const router = useRouter();
    const route = useRoute();
    const switchable = ref(false);
    // 获取router 信息
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const chosenAddressId = ref('');
    const list = ref([]);
    onMounted(() => {
      if (route.query.type === 'addOrder') {
        switchable.value = true;
      }
      getAddress().then((res) => {
        list.value = res.data.data.map((item) => {
          if (item.is_default === 1) {
            chosenAddressId.value = item.id;
          }
          return {
            id: item.id,
            name: item.name,
            tel: item.phone,
            address: item.province + item.city + item.county + item.address,
            isDefault: item.is_default === 1,
          };
          // eslint-disable-next-line no-unreachable
          if (list.value.length === 0) {
            Toast('暂无地址，请前去添加地址');
          }
        });
      });
    });
    const onAdd = () => {
      router.push({ path: '/AddressEdit', query: { type: 'add' } });
    };
    const onEdit = (item) => {
      router.push({ path: '/AddressEdit', query: { type: 'edit', id: item.id } });
    };
    const switchAddress = (item) => {
      UpdateAddressDefault(item.id).then((res) => {
        if (res.status === 204) {
          router.push({ path: '/order' });
        }
      });
    };
    return {
      onClickLeft,
      list,
      onAdd,
      onEdit,
      chosenAddressId,
      switchAddress,
      switchable,
    };
  },
};
</script>

