<template>
  <div class="detail">
    <van-sticky>
    <van-nav-bar title="商品详情" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!--  详情页轮播图-->
    <div>
      <van-swipe :autoplay="3000" lazy-render>
        <van-swipe-item>
          <img alt="123" :src="detailGoods.cover_url" height="380" />
        </van-swipe-item>
      </van-swipe>
      <van-card
        :price="detailGoods.price"
        :desc="detailGoods.description"
        :title="detailGoods.title "
      />
    </div>
    <van-tabs v-model:active="activeName">
      <van-tab  title="概述" name="a">
        <div id="contentA" v-html="detailGoods.details"></div>
      </van-tab>
      <van-tab title="热评" name="b">暂无热评</van-tab>
      <van-tab title="相关商品" name="c">
        <GridGoods :colNum="2" :icon-size="150">
          <van-grid-item
            @click="update"
            v-for="item in likeGoods"
            :key="item.id"
            :icon="item.cover_url"
            :text="item.title+'￥'+item.price+'元'"
            :to="`/detail/${item.id}`"/>
        </GridGoods>
      </van-tab>
    </van-tabs>
    <!--  底部-->
    <van-action-bar safe-area-inset-bottom z-index="999">
      <van-action-bar-icon icon="chat-o" text="客服" color="#ee0a24" />
      <van-action-bar-icon
        :icon="collectionIcon"
        :text="collectionState"
        @click="CollectionS"
        color="#ff5000" />
      <van-action-bar-button type="warning" @click="cars" text="加入购物车" />
      <van-action-bar-button type="danger" text="立即购买" @click="ToBuy" />
    </van-action-bar>

  </div>

</template>

<script>
import {
  onMounted, ref,
} from 'vue';
import { useRoute, useRouter } from 'vue-router';
// eslint-disable-next-line import/no-cycle
import { getDetailAll } from '@/network/about';
// eslint-disable-next-line import/no-cycle
import { getCollects, getCollectsTwo } from '@/network/collection';
import GridGoods from '@/components/common/GridGoods.vue';
// eslint-disable-next-line import/no-cycle
import store from '@/store';
// eslint-disable-next-line import/no-cycle
import { getCars } from '@/network/cars';

export default {
  name: 'DetailS',
  components: {
    GridGoods,
  },
  setup() {
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const collectionState = ref('收藏');
    const collectionIcon = ref('like-o');
    const route = useRoute();
    const router = useRouter();
    const detailGoods = ref({});
    const likeGoods = ref([]);
    const collectionList = ref([]);
    store.state.loading = true;
    const Detailid = route.params.id;
    onMounted(() => {
      getDetailAll(Detailid).then((res) => {
        console.log(res.data);
        detailGoods.value = res.data.goods;
        likeGoods.value = res.data.like_goods;
        console.log(likeGoods.value);
        store.state.loading = false;
      });
      getCollects().then((res) => {
        collectionList.value = res.data.data;
        // eslint-disable-next-line no-plusplus
        for (let i = 0; i < collectionList.value.length; i++) {
          // eslint-disable-next-line eqeqeq
          if (collectionList.value[i].goods_id == route.params.id) {
            collectionState.value = '已收藏';
            collectionIcon.value = 'star';
          }
        }
        console.log(res.data.data);
      });
    });
    function update() {
      // eslint-disable-next-line no-restricted-globals
      location.reload();
    }
    const cars = () => {
      getCars(route.params.id).then((res) => {
        store.dispatch('upCarList');
        console.log(res);
      });
    };
    const CollectionS = () => {
      getCollectsTwo(Detailid).then((res) => {
        if (res.status !== 201) {
          console.log(res);
          collectionState.value = '收藏';
          collectionIcon.value = 'like-o';
        } else {
          console.log(res);
          collectionState.value = '已收藏';
          collectionIcon.value = 'star';
        }
      });
    };
    function ToBuy() {
      getCars(route.params.id).then((res) => {
        store.dispatch('upCarList');
        console.log(res);
      });
      router.push({ path: '/shopCar' });
    }
    return {
      onClickLeft,
      CollectionS,
      detailGoods,
      collectionIcon,
      likeGoods,
      update,
      ToBuy,
      collectionState,
      cars,
    };
  },
};
</script>

