// eslint-disable-next-line import/no-cycle
import { request } from '@/network/request';

// eslint-disable-next-line import/prefer-default-export
export function getCars(goods_id = 0, num = 1) {
  return request({
    url: 'carts',
    method: 'post',
    data: {
      goods_id,
      num,
    },
  });
}
export function getCarsList() {
  return request({
    url: 'carts?include=goods',
  });
}
export function setCarChecked(cart_ids = [0]) {
  return request({
    url: 'carts/checked',
    method: 'patch',
    data: {
      cart_ids,
    },
  });
}
export function setCarNum(cart = 0, num = 1) {
  return request({
    url: `carts/${cart}`,
    method: 'put',
    data: {
      num,
    },
  });
}
export function DelCar(cart = 0) {
  return request({
    url: `carts/${cart}`,
    method: 'delete',
  });
}
export function SetSlect(data) {
  return request({
    url: 'carts/checked',
    method: 'PATCH',
    data,
  });
}
