<template>
  <van-nav-bar :title="$route.query.type==='add'?'新增地址':'更新地址'"
               left-arrow @click-left="onClickLeft" />
  <van-address-edit
    :area-list="areaList"
    :address-info="info"
    :show-delete="deletes"
    show-set-default
    :area-columns-placeholder="['请选择', '请选择', '请选择']"
    @save="onSave"
    @change-default="onChangeDefault"
    @delete="onDelete"
  />
</template>

<script>
import { Dialog, Toast } from 'vant';
import { areaList } from '@vant/area-data';
// eslint-disable-next-line import/no-cycle
import {
  DeleteAddress,
  PostGetAddress,
  setAddressInfo,
  UpdateAddressDefault,
  UpdateAddressInfo,
} from '@/network/address';
import { useRouter, useRoute } from 'vue-router';
import { onMounted, ref } from 'vue';

export default {
  name: 'AddressEdit',
  setup() {
    const router = useRouter();
    const route = useRoute();
    const info = ref({});
    const deletes = ref(false);
    onMounted(() => {
      console.log(route.query.type);
      if (route.query.type === 'edit') {
        deletes.value = true;
        setAddressInfo(route.query.id).then((res) => {
          let areaCode = '';
          console.log(areaList);
          // eslint-disable-next-line no-restricted-syntax
          for (const key in areaList.county_list) {
            if (areaList.county_list[key] === res.data.county) {
              console.log(res.data.county);
              // eslint-disable-next-line no-unused-vars
              areaCode = key;
              console.log(areaCode);
            }
          }
          info.value = {
            name: res.data.name,
            tel: res.data.phone,
            province: res.data.province,
            city: res.data.city,
            county: res.data.county,
            addressDetail: res.data.address,
            areaCode,
            isDefault: res.data.isDefault === 1,
          };
        });
      }
    });
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const onSave = (content) => {
      console.log(content);
      const datas = {
        name: content.name,
        address: content.addressDetail,
        phone: content.tel,
        province: content.province,
        city: content.city,
        county: content.county,
        is_default: content.isDefault ? 1 : 0,
      };
      if (route.query.type === 'add') {
        PostGetAddress(datas).then((res) => {
          console.log(res);
          if (res.status === 201) {
            Toast.success('添加成功');
            router.push({ path: '/addressList' });
          }
        });
      } else {
        UpdateAddressInfo(route.query.id, datas).then((res) => {
          console.log(res);
          if (res.status === 204) {
            Toast.success('更新成功');
            router.push({ path: '/addressList' });
          }
        });
      }
    };
    const onChangeDefault = () => {
      UpdateAddressDefault(route.query.id).then((res) => {
        if (res.status === 204) {
          Toast.success('设置成功');
          router.push({ path: '/addressList' });
        }
      });
    };
    const onDelete = () => {
      Dialog.confirm({
        title: '删除地址',
        message:
          '是否确认删除。',
      })
        .then(() => {
          DeleteAddress(route.query.id).then((res) => {
            if (res.status === 204) {
              Toast.success('删除成功');
              router.push({ path: '/addressList' });
            }
          });
        })
        .catch(() => {
          // on cancel
        });
    };
    return {
      onSave,
      areaList,
      info,
      onClickLeft,
      onChangeDefault,
      onDelete,
      deletes,
    };
  },
};

</script>

