<template>
  <van-nav-bar title="生成订单" left-arrow @click-left="onClickLeft" />
<!--  联系人列表-->
  <van-contact-card type="edit" @click="changeAddress"
                    :tel="datas.address.phone"
                    :name="datas.address.name"></van-contact-card>
<!--  商品列表-->
  <van-card
    v-for="item in datas.goods"
    :key="item.id"
    :num="item.num"
    :price="item.goods.price"
    :desc="item.goods.description"
    :title="item.goods.title"
    :thumb="item.goods.cover_url"
  />
  <van-overlay :show="show" @click="show = false">
    <div class="wrapper" @click.stop>
      <div class="block">
        <van-radio-group v-model="payType" @change="tabType">
          <van-radio name="1">支付宝</van-radio>
          <van-radio name="2">微信支付</van-radio>
        </van-radio-group>
        <img :src="payQrCodeUrl" alt="123">
      </div>
    </div>
  </van-overlay>
<!--  提交订单-->
  <van-submit-bar :price="total*100" button-text="付款" @submit="onSubmit" />

</template>

<script>
import { Toast } from 'vant';
import {
  onMounted, reactive, computed, ref,
} from 'vue';
import { useRouter } from 'vue-router';
// eslint-disable-next-line import/no-cycle
import { getOrderOne, getOrderTwo } from '@/network/order';
import { useStore } from 'vuex';
// eslint-disable-next-line import/no-cycle
import { payQrCode, PayStatus } from '@/network/pay';
// eslint-disable-next-line import/no-cycle
import { getCarsList } from '@/network/cars';

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Order',
  setup() {
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const onEdit = () => Toast('edit');
    const payQrCodeUrl = ref('');
    const payAliyun = ref('');
    const payWechat = ref('');
    const payType = ref('1');
    const router = useRouter();
    const show = ref(false);
    const store = useStore();
    const datas = reactive({
      address: {},
      goods: [],
    });
    // eslint-disable-next-line no-return-assign,no-param-reassign
    const total = computed(() => datas.goods.reduce((s, n) => s += n.num * n.goods.price, 0));
    onMounted(() => {
      getCarsList('inclue=goods').then((res) => {
        const goods = res.data.data.filter((item) => item.is_checked === 1);
        if (goods.length <= 0) {
          router.replace({ path: '/shopCar' });
        }
      });
      console.log(total);
      getOrderOne().then((res) => {
        console.log(res);
        console.log(res.data.address.filter((item) => item.is_default === 1));
        // eslint-disable-next-line prefer-destructuring
        datas.address = res.data.address.filter((item) => item.is_default === 1)[0];
        console.log(res);
        datas.goods = res.data.carts;
        console.log(datas.address.id);
        console.log(datas.goods);
      });
    });
    const changeAddress = () => {
      router.push({ path: '/addressList', query: { type: 'addOrder' } });
    };
    const onSubmit = () => {
      getOrderTwo(datas.address.id).then((res) => {
        console.log(res);
        if (res.data.id >= 1) {
          // eslint-disable-next-line no-shadow
          payQrCode(res.data.id, 'aliyun').then((res) => {
            console.log(res);
            payQrCodeUrl.value = res.data.qr_code_url;
            payAliyun.value = res.data.qr_code_url;
          });
          // eslint-disable-next-line no-shadow
          payQrCode(res.data.id, 'wechat').then((res) => {
            console.log(res);
            payWechat.value = res.data.qr_code_url;
          });
          setInterval(() => {
            // eslint-disable-next-line no-shadow
            PayStatus(res.data.id).then((res) => {
              if (res.data === 2) {
                router.push({ path: '/orderList' });
              }
            });
          }, 1000);
          Toast.success('订单创建成功，请尽快支付');
          show.value = true;
          store.dispatch('upCarList');
        }
      }).catch((err) => {
        Toast.fail('订单创建失败');
        console.log(err);
      });
    };
    const tabType = (name) => {
      console.log(name);
      if (name === 1) {
        payQrCodeUrl.value = payAliyun.value;
      } else {
        payQrCodeUrl.value = payWechat.value;
      }
    };
    return {
      onClickLeft,
      tabType,
      onEdit,
      onSubmit,
      payQrCodeUrl,
      show,
      total,
      datas,
      payType,
      changeAddress,
    };
  },
};
</script>

<style scoped>
.van-submit-bar{
  margin-bottom: 0 !important;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 350px;
  height: 350px;
  background-color: #fff;
}
</style>
