<template>
  <van-nav-bar title="订单列表" left-arrow @click-left="onClickLeft" />
  <van-tabs v-model:active="active" @click-tab="clickTab">
    <van-tab name="0" title="全部"></van-tab>
    <van-tab name="1"  title="待付款"></van-tab>
    <van-tab name="2"  title="已付款"></van-tab>
    <van-tab name="3"  title="代签收"></van-tab>
    <van-tab name="4"  title="已签收"></van-tab>
  </van-tabs>
  <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
  <van-list
    v-model:loading="Loading"
    :finished="finished"
    finished-text="没有更多了"
    @load="onLoad"
  >

      <div class="goodsList"  v-for="item in goods" :key="item.id">
        <span>订单编号：<span>{{ item.order_no }}</span></span><br>
        <span>下单时间：<span>{{ item.created_at }} </span></span>
        <van-card
          v-for="keies in item.goods.data"
          :key="keies.id"
          @click="$router.push({ path: '/orderMore', query: { id: item.id } })"
          :price="keies.price"
          :desc="keies.description"
          :title="keies.title"
          :thumb="keies.cover_url"
        />
      </div>
  </van-list>
  </van-pull-refresh>

</template>

<script>
import { onMounted, ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import { getOrderList } from '@/network/order';
import { Toast } from 'vant';

export default {
  name: 'OrderList',
  setup() {
    const goods = ref([]);
    const pages = ref(1);
    const Loading = ref(false);
    const finished = ref(false);
    const refreshing = ref(false);
    const onRefresh = () => {
      setTimeout(() => {
        refreshing.value = false;
        Toast('刷新成功');
      }, 1000);
    };
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    onMounted(() => {
      getOrderList(0, pages).then((res) => {
        console.log(res);
        goods.value = res.data.data;
      });
    });
    const clickTab = (checked) => {
      console.log(checked.name);
      getOrderList(checked.name).then((res) => {
        console.log(res);
        goods.value = res.data.data;
      });
    };
    const onLoad = () => {
      Loading.value = true;
      getOrderList(0, pages).then((res) => {
        pages.value += 1;
        console.log(pages.value);
        goods.value = res.data.data;
        Loading.value = false;
        if (res.data.data.length <= 0) {
          finished.value = true;
        }
      });
    };
    return {
      onClickLeft,
      goods,
      clickTab,
      onLoad,
      Loading,
      finished,
      onRefresh,
      refreshing,
    };
  },
};
</script>

<style scoped lang="scss">
.goodsList{
  margin: 1rem 0;
  padding: 0.5rem 1rem;
  text-align: left;
  background: #f1f1f1;
}
.van-card{
  background: #f1f1f1 !important;
}
</style>
