import { createStore } from 'vuex';
import { getCarsList } from '@/network/cars';

export default createStore({
  state: {
    loading: false,
    carcount: 0,
  },
  getters: {
  },
  mutations: {
    updatecarcount(state, num) {
      state.carcount = num;
    },
  },
  actions: {
    upCarList({ commit }) {
      getCarsList().then((res) => {
        commit('updatecarcount', res.data.data.length);
      });
    },
  },
  modules: {
  },
});
