<template>
  <van-nav-bar title="用户注册" />
  <img src="../../assets/register.jpg" alt="registerBack">
  <van-form @failed="onFailed" @submit="onSubmit">
    <van-cell-group inset>
      <!-- 用户名 -->
      <van-field
        v-model="name"
        name="name"
        label="用户名"
        placeholder="请输入用户名"
        :rules="[{ require: true, message: '请输入正确内容' }]"
      />
      <!-- 密码 -->
      <van-field
        v-model="password"
        name="password"
        label="密码"
        placeholder="请输入密码"
        type="password"
        :rules="[{ require: true, message: '请输入6位数字' },{ patternPwd, message: '请输入正确内容' }]"
      />
      <!-- 请确认密码 -->
      <van-field
        v-model="password_confirmation"
        name="password_confirmation"
        placeholder="请确认密码"
        label="确认密码"
        type="password"
        :rules="[{ require: true, message: '请输入正确内容' },{ patternPwd, message: '请输入正确内容' }]"
      />
      <!-- 请输入邮箱 -->
      <van-field
        v-model="email"
        name="email"
        placeholder="请输入邮箱"
        label="邮箱"
        :rules="[{ require: true, message: '请输入正确内容' },{ patternEmail, message: '请输入正确内容' }]"
      />
    </van-cell-group>
    <div style="margin: 16px;">
      <p @click="goLogin" @keyup="goLogin">已有账号，点击登录</p>
     <van-button round block color="linear-gradient(to right,#ff6034,#ee0a24)" native-type="submit">
     注册
     </van-button>
    </div>
  </van-form>
</template>

<script>
import { reactive, toRefs } from 'vue';
// eslint-disable-next-line import/no-cycle
import { PostRegister } from '@/network/user';
import { Toast } from 'vant';
import { useRouter } from 'vue-router';

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Register',
  setup() {
    const patternPwd = /\d{6}/;
    const patternEmail = /^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/;
    const data = reactive({
      name: '',
      email: '',
      password: '',
      password_confirmation: '',
    });
    const router = useRouter();
    const onSubmit = () => {
      console.log(data.name);
      console.log(data.password);
      console.log(data.email);
      console.log(data.password_confirmation);
      if (data.password !== data.password_confirmation) {
        Toast.fail('密码前后不一致，请重新输入');
      } else {
        PostRegister(data).then((res) => {
          console.log(res);
        });
        router.push({ path: '/login' });
      }
    };
    function goLogin() {
      router.push({ path: '/login' });
    }
    return {
      ...toRefs(data),
      onSubmit,
      patternPwd,
      patternEmail,
      goLogin,
    };
  },
};
</script>

<style scoped>
img{
  width:100%
}
</style>
