import { request } from '@/network/request';

// eslint-disable-next-line import/prefer-default-export
export function getCollects() {
  return request({
    url: 'collects',
  });
}
export function getCollectsTwo(id) {
  return request({
    url: `collects/goods/${id}`,
    method: 'post',
  });
}
