<template>
  <van-nav-bar title="更改密码" left-arrow @click-left="onClickLeft"  />
  <van-form @submit="onSubmit">
    <van-cell-group inset>
      <!-- 输入密码 -->
      <van-field v-model="code" label="验证码" />
      <van-field v-model="email" label="新密码" />
      <van-field v-model="password" t label="确认密码" />
      <van-field v-model="passwordReNew" t label="确认密码" />
    </van-cell-group>
    <div style="margin: 16px;">
      <van-button round block type="primary" native-type="submit">
        更改
      </van-button>
    </div>
  </van-form>
</template>

<script>
import { ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import { findPasswords } from '@/network/user';

export default {
  name: 'SetPassword',
  setup() {
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const code = ref('');
    const email = ref('');
    const password = ref('');
    const passwordReNew = ref('');
    const onSubmit = () => {
      findPasswords(code.value, email.value, password.value, passwordReNew.value).then((res) => {
        if (res.status === 204) {
          alert('更新成功');
          // eslint-disable-next-line no-restricted-globals
          history.back();
        }
      });
    };
    return {
      code,
      passwordReNew,
      password,
      email,
      onSubmit,
      onClickLeft,
    };
  },
};
</script>

