<template>
  <div class="orderbody">
    <van-nav-bar title="订单详情" left-arrow  @click-left="onClickLeft"/>
    <div class="ordermore">
      <h4>订单状态：<span>{{Status}}</span></h4>
      <h4>订单编号：<span>{{orderNum}}</span></h4>
      <h4>下单时间：<span>{{orderTime}}</span></h4>
      <van-button v-show="showPay" @click="PayOrder" type="primary" size="large">立即付款</van-button>
    </div>
    <div class="ordermore">
      <h4>商品金额：<span>{{PriceAll}}元</span></h4>
      <h4>配送方式：<span>顺丰速运</span></h4>
    </div>
    <van-card
      v-for="item in goods"
      :key="item.id"
      :price="item.price"
      :desc="item.description"
      :title="item.title"
      :thumb="item.cover_url"
    />
    <van-overlay :show="show" @click="show = false">
      <div class="wrapper" @click.stop>
        <div class="block">
          <van-radio-group v-model="payType" @change="tabType">
            <van-radio name="1">支付宝</van-radio>
            <van-radio name="2">微信支付</van-radio>
          </van-radio-group>
          <img :src="payQrCodeUrl" alt="123">
        </div>
      </div>
    </van-overlay>
    <footers></footers>
  </div>

</template>

<script>
import footers from '@/components/common/footers.vue';
import { onMounted, ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import { getOrderDetail, getOrderExpress } from '@/network/order';
import { useRoute } from 'vue-router';
import { useStore } from 'vuex';
// eslint-disable-next-line import/no-cycle
import { payQrCode } from '@/network/pay';
import { Toast } from 'vant';

export default {
  name: 'OrderMore',
  components: {
    footers,
  },
  setup() {
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const goods = ref({});
    const route = useRoute();
    const Status = ref('待支付');
    const store = useStore();
    const payType = ref('1');
    const orderTime = ref('');
    const showPay = ref(false);
    const orderNum = ref('');
    const PriceAll = ref('');
    const show = ref(false);
    const payQrCodeUrl = ref('');
    const payAliyun = ref('');
    const payWechat = ref('');
    onMounted(() => {
      console.log(route.query.id);
      getOrderDetail(route.query.id).then((res) => {
        console.log(res);
        goods.value = res.data.goods.data;
        orderTime.value = res.data.created_at;
        orderNum.value = res.data.order_no;
        PriceAll.value = res.data.amount;

        if (res.data.status === 1) {
          Status.value = '待支付';
          showPay.value = true;
        } else if (res.data.status === 2) {
          Status.value = '已付款';
        } else if (res.data.status === 3) {
          Status.value = '待发货';
        } else if (res.data.status === 4) {
          Status.value = '代签收';
        } else if (res.data.status === 5) {
          Status.value = '订单完成';
        }
      });
      getOrderExpress(route.query.id).then((res) => {
        console.log(res);
      });
    });
    const PayOrder = () => {
      payQrCode(route.query.id, 'aliyun').then((res) => {
        console.log(res);
        payQrCodeUrl.value = res.data.qr_code_url;
        payAliyun.value = res.data.qr_code_url;
      });
      // eslint-disable-next-line no-shadow
      payQrCode(route.query.id, 'wechat').then((res) => {
        console.log(res);
        payWechat.value = res.data.qr_code_url;
      });
      Toast.success('订单创建成功，请尽快支付');
      show.value = true;
      store.dispatch('upCarList');
    };
    const tabType = (name) => {
      console.log(name);
      if (name === 1) {
        payQrCodeUrl.value = payAliyun.value;
      } else {
        payQrCodeUrl.value = payWechat.value;
      }
    };
    return {
      goods,
      Status,
      orderTime,
      orderNum,
      payType,
      PriceAll,
      onClickLeft,
      showPay,
      PayOrder,
      tabType,
      show,
    };
  },
};
</script>

