.orderbody{
  background: #eeeeee;
  height: 100vh;
}
.ordermore{
  text-align: left;
  background: #fff;
  border-radius: 0.5rem;
  padding: 0 1rem;
  margin: 0 1rem;
}
.van-card{
  margin: 0 1rem;
  border-radius: 0.5rem;
}
.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.block {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 350px;
  height: 350px;
  background-color: #fff;
}
</style>
