import { request } from '@/network/request';

// eslint-disable-next-line import/prefer-default-export
export function getHomeAll(id) {
  return request({
    url: `index?id=${id}`,
  });
}
export function getHomeGoods(type = 'sales', page = 1) {
  return request({
    url: `index?${type}=1&page=${page}`,
  });
}
