<template>
  <van-nav-bar title="编辑个人信息" left-arrow @click-left="onClickLeft"  />
  <van-field name="uploader" label="更改头像">
    <template #input>
      <van-uploader v-model="value" />
    </template>
  </van-field>
  <van-form @submit="onSubmit">
    <van-cell-group inset>
      <van-field
        v-model="username"
        name="username"
        label="更改用户名"
        placeholder="用户名"
        :rules="[{ required: true, message: '请填写用户名' }]"
      />
    </van-cell-group>
    <van-cell title="修改密码" is-link to="/SetPassword" />
    <van-cell title="手机号" is-link value="绑定手机号" to="/setPhone" />
    <van-cell title="邮箱" is-link :value="email" to="/setEmail" />
    <van-cell title="找回密码" is-link @click="getCode" />
    <div style="margin: 16px;">
      <van-button round block type="primary" native-type="submit">
        更改
      </van-button>
    </div>
  </van-form>
</template>

<script>
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
// eslint-disable-next-line import/no-cycle
import {
  findPassword,
  GetOssToken, GetUser, PostSetUser, SentEmailSms,
} from '@/network/user';
import { Toast } from 'vant';

export default {
  name: 'setUser',
  setup() {
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const router = useRouter();
    const value = ref([{ url: 'https://img.yzcdn.cn/vant/leaf.jpg' }]);
    const afterRead = (file) => {
      // 此时可以自行将文件上传至服务器
      console.log(file);
    };
    const user = ref({});
    const sms = ref('');
    const phone = ref('');
    const username = ref('');
    const email = ref('');
    const sentSms = () => {
      SentEmailSms({ email }).then((res) => {
        console.log(email);
        console.log(res);
      });
    };
    const onSubmit = (values) => {
      phone.value = values.phone;
      console.log(phone.value);
      PostSetUser(values.username).then((res) => {
        console.log(res);
      });
      console.log('submit', values);
    };
    onMounted(() => {
      GetUser().then((res) => {
        console.log(res);
        phone.value = res.data.phone;
        user.value = res.data;
        username.value = res.data.name;
        email.value = res.data.email;
      });
      GetOssToken().then((res) => {
        console.log(res);
      });
    });
    const getCode = () => {
      console.log(email.value);
      findPassword(email.value).then((res) => {
        if (res.status === 204) {
          Toast('验证码发送成功');
          router.push({ path: '/findPassword' });
        }
      });
    };
    return {
      username,
      getCode,
      email,
      onSubmit,
      phone,
      sms,
      value,
      onClickLeft,
      afterRead,
      sentSms,
    };
  },
};
</script>

