import { createRouter, createWebHistory } from 'vue-router';
import setUser from '@/views/user/setUser.vue';
import OrderList from '@/views/order/OrderList.vue';
import SetPhone from '@/views/user/children/SetPhone.vue';
import setEmail from '@/views/user/children/SetEmail.vue';
import SetPassword from '@/views/user/children/SetPassword.vue';
import findPassword from '@/views/user/children/findPassword.vue';
import HomeView from '../views/Home/HomeView.vue';
import AddressList from '../views/AddressList/AddressList.vue';
import AddressEdit from '../views/AddressEdit/AddressEdit.vue';
import Register from '../views/user/Register.vue';
import DetailS from '../views/details/DetailS.vue';
import Login from '../views/user/Login.vue';
import order from '../views/order/Order.vue';
import orderMore from '../views/order/OrderMore.vue';
import Collection from '../views/Collection/Collection.vue';
import AboutUs from '../views/user/AboutUs.vue';

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
  },
  {
    path: '/AddressList',
    name: 'AddressList',
    component: AddressList,
  },
  {
    path: '/AddressEdit',
    name: 'AddressEdit',
    component: AddressEdit,
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About/AboutView.vue'),
  },
  {
    path: '/shopCar',
    name: 'shopCar',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/car/shopCar.vue'),
    meta: {
      requireAuth: true,
    },
  },
  {
    path: '/indexMe',
    name: 'indexMe',
    component: () => import(/* webpackChunkName: "about" */ '../views/user/indexMe.vue'),
    meta: {
      requireAuth: true,
    },
  },
  {
    path: '/detail/:id',
    name: 'detail',
    component: DetailS,
  },
  {
    path: '/register',
    name: 'register',
    component: Register,
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
  },
  {
    path: '/setUser',
    name: 'setUser',
    component: setUser,
  },
  {
    path: '/order',
    name: 'order',
    component: order,
  },
  {
    path: '/orderMore',
    name: 'orderMore',
    component: orderMore,
  },
  {
    path: '/collection',
    name: 'collection',
    component: Collection,
  },
  {
    path: '/orderList',
    name: 'orderList',
    component: OrderList,
  },
  {
    path: '/aboutUs',
    name: 'aboutUs',
    component: AboutUs,
  },
  {
    path: '/setPhone',
    name: 'setPhone',
    component: SetPhone,
  },
  {
    path: '/setEmail',
    name: 'setEmail',
    component: setEmail,
  },
  {
    path: '/SetPassword',
    name: 'SetPassword',
    component: SetPassword,
  },
  {
    path: '/findPassword',
    name: 'findPassword',
    component: findPassword,
  },
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});
router.beforeEach((to, from, next) => {
  if (to.meta.requireAuth && !window.localStorage.getItem('token')) {
    next({ name: 'login' });
  } else next();
});
export default router;
