<template>
  <van-nav-bar title="更改密码" left-arrow @click-left="onClickLeft"  />
  <van-form @submit="onSubmit">
    <van-cell-group inset>
      <!-- 输入密码 -->
      <van-field v-model="passwordOld" type="password" label="旧密码" />
      <van-field v-model="passwordNew" type="password" label="新密码" />
      <van-field v-model="passwordReNew" type="password" label="确认密码" />
    </van-cell-group>
    <div style="margin: 16px;">
      <van-button round block type="primary" native-type="submit">
        更改
      </van-button>
    </div>
  </van-form>
</template>

<script>
import { ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import { SentPassword } from '@/network/user';

export default {
  name: 'SetPassword',
  setup() {
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const passwordOld = ref('');
    const passwordNew = ref('');
    const passwordReNew = ref('');
    const onSubmit = () => {
      if (passwordNew.value === passwordReNew.value) {
        SentPassword(passwordOld.value, passwordNew.value, passwordReNew.value).then((res) => {
          console.log(res);
          if (res.status === 204) {
            // eslint-disable-next-line no-restricted-globals
            history.back();
          }
        });
      }
    };
    return {
      passwordOld,
      passwordReNew,
      passwordNew,
      onSubmit,
      onClickLeft,
    };
  },
};
</script>

