import { request } from '@/network/request';

// eslint-disable-next-line import/prefer-default-export
export function getAddress() {
  return request({
    url: 'address',
  });
}
export function PostGetAddress(data) {
  return request({
    url: 'address',
    method: 'POST',
    data,
  });
}
export function setAddressInfo(address) {
  return request({
    url: `address/${address}`,
  });
}
// eslint-disable-next-line default-param-last
export function UpdateAddressInfo(address = 0, data) {
  return request({
    url: `address/${address}`,
    method: 'PUT',
    data,
  });
}
export function UpdateAddressDefault(address) {
  return request({
    url: `address/${address}/default`,
    method: 'PATCH',
  });
}
export function DeleteAddress(address) {
  return request({
    url: `address/${address}`,
    method: 'DELETE',
  });
}
