<template>
  <van-nav-bar title="绑定手机号" left-arrow @click-left="onClickLeft"  />
  <van-nav-bar title="绑定手机号" left-arrow @click-left="onClickLeft"  />
  <van-form @submit="onSubmit">
    <van-cell-group inset>
      <van-field
        v-model="phone"
        name="phone"
        label="绑定手机号"
        placeholder="绑定手机号"
        :rules="[{ required: true, message: '绑定手机号' }]"
      />
      <van-field
        v-model="PhoneCode"
        center
        clearable
        label="短信验证码"
        placeholder="请输入短信验证码"
      >
        <template #button>
          <van-button size="small" @click="phondeCodes" type="primary">发送验证码</van-button>
        </template>
      </van-field>
    </van-cell-group>
    <div style="margin: 16px;">
      <van-button round block type="primary" native-type="submit">
        更改
      </van-button>
    </div>
  </van-form>
</template>

<script>
import { ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import { SentPhone, SentPhoneNum } from '@/network/user';

export default {
  name: 'SetPhone',
  setup() {
    const phone = ref('');
    const PhoneCode = ref('');
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const phondeCodes = () => {
      SentPhoneNum(phone.value).then((res) => {
        console.log(phone);
        console.log(res);
      });
    };
    const onSubmit = (content) => {
      SentPhone(phone.value, PhoneCode.value).then((res) => {
        console.log(res);
        if (res.status === 204) {
          // eslint-disable-next-line no-restricted-globals
          history.back();
        }
      });
      console.log(content);
    };
    return {
      onClickLeft,
      phondeCodes,
      PhoneCode,
      phone,
      onSubmit,
    };
  },
};
</script>

