import { request } from '@/network/request';

// eslint-disable-next-line import/prefer-default-export
export function getOrderOne() {
  return request({
    url: 'orders/preview',
  });
}
// eslint-disable-next-line default-param-last
export function getOrderTwo(id) {
  return request({
    // eslint-disable-next-line camelcase
    url: '/orders',
    method: 'post',
    data: {
      address_id: id,
    },
  });
}
export function getOrder() {
  return request({
    url: 'orders',
    method: 'post',
  });
}
export function getOrderList(status, page) {
  return request({
    url: `orders?status=${status}&page=${page}&include=goods,user,orderDetails`,
  });
}
export function getOrderDetail(order) {
  return request({
    url: `orders/${order}?include=goods,user,orderDetails`,
  });
}
export function getOrderExpress(order) {
  return request({
    url: `orders/${order}/express`,
  });
}
