<template>
  <div class="home">
    <van-nav-bar title="图书商城" />
    <!--  搜索框-->
    <SearchS></SearchS>
    <!--  轮播图-->
    <swiperImg style="background: #fbedcf" :slides="slides"></swiperImg>
    <!--  推荐商品-->
    <GridGoods class="goodsSkill" :home="home" :colNum="5" :icon-size="'50px'">
      <van-grid-item
        v-for="item in home"
        :key="item.id"
        :icon="item.cover_url"
        :text="item.title"
        :to="`/detail/${item.id}`"/>
    </GridGoods>
    <!--通知-->
    <img style="width: 90%;margin: 0.5rem auto" alt="middle" src="../../assets/img/middle.png">
    <NoticeS></NoticeS>
    <ul class="mind">
      <li style="float: left">
        <img src="../../assets/img/mind.png" alt="mind">
      </li>
      <li style="float: right">
        <img src="../../assets/img/big.png" alt="big">
        <img src="../../assets/img/perfect.png" alt="perfect">
      </li>
    </ul>
    <!--  商品分类-->
    <van-sticky>
      <van-tabs v-model:active="active"  @click-tab="onClickTab">
        <van-tab title="畅销" name="sales"></van-tab>
        <van-tab title="新书" name="new"></van-tab>
        <van-tab title="精选" name="recommend"></van-tab>
      </van-tabs>
    </van-sticky>
    <div class="wrapper">
        <!--  商品列表-->
        <goodsList>
          <GridGoods :colNum="2" :icon-size="150">
            <van-grid-item class="goodsList"
                           v-for="item in GoodsShow"
                           @click="$router.push({path:`/detail`,params:{id:item.id}});"
                           :key="item.id"
                           :icon="item.cover_url"
                           :text="item.title+'￥'+item.price+'元'"
            />
          </GridGoods>
        </goodsList>
      <div style="height: 50px"></div>
    </div>
    <footerS></footerS>
  </div>

</template>

<script>
import goodsList from '@/views/Home/children/goodsList.vue';
import swiperImg from '@/views/Home/children/swiperImg.vue';
import SearchS from '@/components/common/SearchS.vue';
import GridGoods from '@/components/common/GridGoods.vue';
import NoticeS from '@/components/common/noticeS.vue';
// eslint-disable-next-line import/no-cycle
import { getHomeAll, getHomeGoods } from '@/network/home';
import footerS from '@/components/common/footers.vue';
import BScroll from 'better-scroll';
import {
  reactive, ref, computed, onMounted,
} from 'vue';
import { useStore } from 'vuex';

export default {
  name: 'HomeView',
  components: {
    swiperImg,
    SearchS,
    GridGoods,
    NoticeS,
    footerS,
    goodsList,
  },
  data() {
    return {
      home: [],
      slides: [],
    };
  },
  mounted() {
    const store = useStore();
    store.state.loading = true;
    getHomeAll().then((res) => {
      console.log(res);
      this.home = res.data.goods.data;
      this.slides = res.data.slides;
      store.state.loading = false;
    });
  },
  setup() {
    const goods = reactive({
      sales: { page: 1, list: [] },
      recommend: { page: 1, list: [] },
      new: { page: 1, list: [] },
    });
    const type = ref('sales');
    const GoodsShow = computed(() => goods[type.value].list);
    onMounted(() => {
      getHomeGoods('sales').then((res) => {
        goods.sales.list = res.data.goods.data;
      });
      getHomeGoods('recommend').then((res) => {
        goods.recommend.list = res.data.goods.data;
      });
      getHomeGoods('new').then((res) => {
        goods.new.list = res.data.goods.data;
      });
      const bScorll = new BScroll(document.querySelector('.wrapper'), {
        probeType: 3,
        pullUpLoad: true,
      });
      bScorll.on('pullingUp', () => {
        const pages = goods[type.value].page + 1;
        getHomeGoods(type.value, pages).then((res) => {
          goods[type.value].list.push(...res.data.goods.data);
        });
        goods[type.value].page += 1;
        bScorll.finishPullUp();
      });
    });

    const onClickTab = ({ name }) => {
      type.value = name;
    };
    return {
      onClickTab,
      goods,
      GoodsShow,
    };
  },
};
</script>
<style>
.goodsSkill{
  background: url("../../assets/img/homeimg.png") no-repeat;
  background-size: cover;
}
.mind{
  overflow: hidden;
  margin-top: 0.5rem;
}
.mind img{
  width: 90%;
}
.mind li:first-child{
  float: left;
  width: 50%;
}
.mind li:last-child{
  float: right;
  width: 50%;
}
.mind li:last-child img:last-child{
  margin-top: 2rem;
}
.home{
  height: 100vh;
}
</style>
