<template>
  <van-nav-bar title="收藏" left-arrow @click-left="onClickLeft"  />
  <van-swipe-cell>
    <van-card
      v-for="item in collectionList" :key="item.goods.id"
      :price="item.goods.price"
      :desc="item.goods.description"
      :title="item.goods.title"
      class="goods-card"
      :thumb="item.goods.cover_url"
      @click="goDetail(item.goods_id)"
    >
    </van-card>`
  </van-swipe-cell>
</template>

<script>
import { onMounted, ref } from 'vue';
// eslint-disable-next-line import/no-cycle
import { getCollects } from '@/network/collection';
import { useRouter } from 'vue-router';

export default {
  // eslint-disable-next-line vue/multi-word-component-names
  name: 'Collection',
  setup() {
    const collectionList = ref([]);
    // eslint-disable-next-line no-restricted-globals
    const onClickLeft = () => history.back();
    const router = useRouter();
    onMounted(() => {
      getCollects().then((res) => {
        collectionList.value = res.data.data;
        console.log(collectionList.value);
        console.log(res);
      });
    });
    function goDetail(Goodsid) {
      const id = Goodsid;
      router.push(`/detail/${id}`);
    }
    return {
      onClickLeft,
      collectionList,
      goDetail,
    };
  },
};
</script>

<style scoped>
.goods-card {
  margin: 0;
  background-color: #fff;
}

.delete-button {
  height: 100%;
}
</style>
